import React, { useState } from "react";
import style from "./EachPost.module.css";
import "./EachPost.css";
import ReadMoreText from "../ReadMoreText";
import PostContainer from "../PostContainer";
import CreateButton from "../../../../../../../optimized/Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import { useNavigate, useParams } from "react-router-dom";

function EachPostAnnouncement({ post }) {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const params = useParams();

  // console.info("post?.new_announcement", post?.new_announcement);

  const onNavigate = () => {
    navigate(`/q/${params.username}/notifications?a=announcement&n=detail`, {
      state: {
        annId: post?.new_announcement?._id,
      },
    });
  };
  return (
    <PostContainer
      author={post?.author}
      authorPhotoId={post?.authorPhotoId}
      authorProfilePhoto={post?.authorProfilePhoto}
      postStatus={post?.postStatus}
      authorUserName={post?.authorUserName}
      createdAt={post?.createdAt}
      whichType="ANNOUNCEMENT"
    >
      {" "}
      {post?.new_announcement?.insAnnTitle ? (
        <div className={style.postText}>
          <ReadMoreText
            text={post?.new_announcement?.insAnnTitle}
            stat="title"
            parentStyle={style.text}
          />
        </div>
      ) : null}
      <CreateButton
        onAction={onNavigate}
        label={t("view_more_detail")}
        customStyle={{
          marginInline: "1rem",
        }}
      />
    </PostContainer>
  );
}

export default EachPostAnnouncement;
